import avatar from '@/components/AVATAR/'
export default {
  name: 'CommentList',
  components: {
    avatar
  },
  props: {
    commentList: {
      type: Array,
      default: () => []
    },
    cntComment: {
      type: Number,
      default: 0
    }
  },
  data () {
    return {}
  },
  methods: {
    // 点赞(将评论的id传递过去)
    clickLike (commentId, index) {
      this.$emit('clickLike', commentId, index)
    },
    showComment () {
      this.$emit('showComment')
    },
    // 点击用户头像
    clickAvatar (uid) {
      this.$emit('clickAvatar', uid)
    }
  }
}



