import avatar from '@/components/AVATAR/'
export default {
  name: 'PublishTime',
  components: {
    avatar
  },
  props: {
    userinfo: {
      type: Object,
      default: () => {}
    }
  },
  mounted () {},
  data () {
    return {}
  },
  methods: {
    // 点击用户头像
    clickAvatar () {
      this.$emit('clickAvatar', this.userinfo.uid)
    }
  }
}



